import React from "react";
import s from "./Work.module.scss"

type PropsType = {
    title: string
    description: string
}
export const Work = ({title, description}: PropsType) => {
    return (
        <div className={s.main}>
            <div className={s.container}>

                <div className={s.image}>
                    <img
                        src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/bd7039f6-9d6a-4365-85a3-c7cdaa31c873/d5ku9pk-dac0bc61-12d8-4fbf-bd5c-9031d262f418.gif?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..3NrvCTaDrOqn57eKhNiu_b7XC1eaNUAlXzgSKkU-c74"
                        alt="sas"/>
                    <a href="123">check it</a>
                </div>


                <div className={s.wrapper}>
                    <div>{title}</div>
                    <div className={s.description}>
                        <span>{description}</span>
                    </div>
                </div>
            </div>
        </div>
    )
}